
import { ref, defineComponent, reactive, unref } from 'vue'
import { useRouter } from 'vue-router'
import '../mock/login.js'
import { login } from '../axios'

// 
interface loginData {
  email: string;
  pass: string;
}

export default defineComponent({
  setup () {
    const loginForm = ref<loginData>({
      email: '',
      pass: ''
    })
    const router = useRouter()
    const loginFormRef = ref()
    // 
    const validatePass = (rule, value, callback) => {
      //  _.
      const reg = /^[A-Za-z0-9][A-Za-z0-9_.]{5,14}$/
      if (!value.match(reg)) {
        callback(new Error(',,.'))
      } else {
        callback()
      }
    }
    // 
    const loginRules = reactive({
      email: [
        { required: true, message: '', trigger: 'blur' },
        { type: 'email', message: '', trigger: 'blur' }
      ],
      pass: [
        { required: true, message: '', trigger: 'blur' },
        { min: 6, max: 15, message: '6~15', trigger: 'blur' },
        { validator: validatePass, trigger: 'blur' }
      ]
    })
    // 
    const successMode = ref<0|1|2>(0)
    // 
    const tip = ref<string>('')
    // 
    const getTip = (message) => {
      tip.value = message
    }
    // 
    const resetForm = () => {
      // 
      // loginForm.value.email = ''
      // loginForm.value.pass = ''
      // 
      const form = unref(loginFormRef)
      form.resetFields()
    }
    // 
    const goto = () => {
      router.push('/')
    }
    // 
    const changeMode = (mode) => {
      successMode.value = mode
    }
    // 
    const submitForm = async () => {
      const form = unref(loginFormRef)
      if (!form) {
        return
      }
      try {
        await form.validate()
        const { email, pass } = loginForm.value
        login(email, pass, goto, changeMode, getTip)
      } catch (err) {
        console.log(err)
      }
    }
    return {
      loginFormRef,
      loginForm,
      loginRules,
      resetForm,
      submitForm,
      successMode,
      goto,
      tip
      // setCookies
    }
  }
})
